<template>
    <ContentBase>
        <div class="card" v-for="user in users" :key="user.id" @click="open_user_profile(user.id)">
            <div class="card-body">
                <div class="row">
                    <div class="col-1 img-field">
                        <!-- 如果某个属性想要写的是变量，而不是字符串, 则在属性名前加':', 是'v-bind:'的缩写 -->
                        <img class="img-fluid" :src="user.photo" alt="">
                    </div>
                    <div class="col-11">
                        <div class="username">{{ user.username }}</div>
                        <div class="follower-count">{{ user.followerCount }}</div>
                    </div>
                </div>
            </div>
        </div>
    </ContentBase>
</template>

<script>
    import ContentBase from '../components/ContentBase';
    // import $ from 'jquery';
    import axios from 'axios';
    import { ref } from 'vue';
    import router from '@/router/index';
    import { useStore } from 'vuex';

    export default {
        name: 'UserListView',
        components: {
            ContentBase,
        },
        setup() {
            const store = useStore();
            let users = ref([]);

            /*
            $.ajax({
                url: 'https://app165.acapp.acwing.com.cn/myspace/userlist/',
                type: 'GET',
                success(resp) {
                    users.value = resp;
                }                
            });
            */
            axios
                .get('https://game.zzqahm.top/myspace/userlist/')
                .then(resp => {
                    users.value = resp.data;
                });

            const open_user_profile = userId => {
                if(store.state.user.is_login) {
                    router.push({
                        name: 'userprofile',
                        params: {
                            userId
                        }
                    });
                } else {
                    router.push({name: 'login'});
                }
            };

            return {
                users,
                open_user_profile,
            };
        }
    }
</script>

<style scoped>
    img {
        border-radius: 50%;
    }

    .username {
        font-weight: bold;
        height: 50%;
    }

    .follower-count {
        font-size: 12px;
        color: gray;
        height: 50%;
    }

    .card {
        margin-bottom: 20px;
        cursor: pointer;
    }
    
    .card:hover {
        box-shadow: 2px 2px 10px lightgray;
        transform: 500ms;
    }

    .img-field {
        display: flex;
        flex-direction: column;
        justify-content: center;
     }
</style>
