<template>
    <div class="card edit-field">
        <div class="card-body">
            <label for="edit-post" class="form-label">编辑帖子</label>
            <!-- v-model: 将textarea的内容和content内容绑定起来 -->
            <textarea v-model="content" class="form-control" id="edit-post" rows="3"></textarea>
            <button type="button" class="btn btn-primary btn-sm" @click="post_a_post">发帖</button>
        </div>
    </div>
</template>

<script>
    import { ref } from 'vue';
    // import $ from 'jquery';
    import axios from 'axios';
    import qs from 'qs';
    import { useStore } from 'vuex';

    export default {
        name: "UserProfileWrite",
        setup(props, context) {
            const store = useStore();
            let content = ref('');
                
            const post_a_post = () => {
                if(content.value.trim() === "") return false;
                else content.value = content.value.trim();
                /*
                $.ajax({
                    url: "https://app165.acapp.acwing.com.cn/myspace/post/",
                    type: "POST",
                    data: {
                        content: content.value,
                    },
                    headers: {
                        'Authorization': "Bearer " + store.state.user.access,
                    },
                    success(resp) {
                        if(resp.result === "success") {
                            context.emit('post_a_post', content.value);
                            content.value = "";
                        }
                    }
                });
                */
                axios
                    .post('https://game.zzqahm.top/myspace/post/', 
                        qs.stringify({ content: content.value }),
                        {
                            // 请求头
                            headers: {
                                'Authorization': "Bearer " + store.state.user.access,
                            },
                        }
                    )
                    .then(resp => {
                        if(resp.data.result === "success") {
                            context.emit('post_a_post', content.value);
                            content.value = "";
                        }
                    })
                    .catch(error => {
                        store.commit('judgeError', error.response.status);
                    });
            };

            return {
                content,
                post_a_post,
            }
        }
    }
</script>

<style scoped>
    .edit-field {
        margin-top: 20px;
    }

    button {
        margin-top: 10px;
    }
</style>