<template>
    <div class="card">
        <div class="card-body">
            <!-- 
                或者写成 <div v-for="(post, index) in posts.post" :key="index">
                用index作为key的前提是数组不变，要不然不能用index作为key
            -->
            <div v-for="post in posts.posts" :key="post.id">
                <div class="card single-post">
                    <div class="card-body">
                        {{ post.content }}
                        <button @click="open(post.id)" v-if="is_me" class="btn btn-danger btn-sm">删除</button>
                    </div>
                </div>
            </div>
        </div>
    </div> 
    
</template>

<script>
    import { computed } from 'vue';
    import { useStore } from 'vuex';
    import $ from 'jquery';
    // import axios from 'axios';
    import { ElMessage, ElMessageBox } from 'element-plus'

    export default {
        name: "UserProfilePosts",
        props: {
            posts: {
                type: Object,
                required: true,
            },
            user: {
                type: Object,
                required: true,
            },
        },
        setup(props, context) {
            const store = useStore();
            let is_me = computed(() => store.state.user.id === props.user.id);
            

            const open = post_id => {
                ElMessageBox.confirm(
                    '您确认删除这个动态码？',
                    '确认删除',
                    {
                        confirmButtonText: '确认',
                        cancelButtonText: '取消',
                        type: 'error',
                    }
                )
                .then(() => {
                $.ajax({
                    url: "https://game.zzqahm.top/myspace/post/",
                    type: "DELETE",
                    data: {
                        post_id,
                    },
                    headers: {
                        'Authorization': "Bearer " + store.state.user.access,
                    },
                    success(resp) {
                        if(resp.result === "success") {
                            context.emit('delete_a_post', post_id);
                            ElMessage({
                                type: 'success',
                                message: '删除成功',
                            })
                        }
                    }
                });

                /*
                // 后台收不到参数
                let data = new FormData();
                data.append("post_id", post_id); 
                axios
                    .delete('/api/myspace/post/', {
                        params: {
                            data
                        },
                        headers: {
                            'Authorization': "Bearer " + store.state.user.access,
                        }
                    })
                    .then(resp => {
                        if(resp.data.result === "success") {
                            context.emit('delete_a_post', post_id);
                        }
                    });
                */
                })
                .catch(() => {
                    ElMessage({
                        type: 'info',
                        message: '取消删除',
                    })
                })
            }

            return {
                is_me,
                open,
            }
        }
    }
</script>

<style scoped>
    .single-post {
        margin-bottom: 10px;
    }

    button {
        float: right;
    }
</style>