<template>
    <div class="card">
        <div class="card-body">
            <div class="row">
                <div class="col-3 img-field">
                    <!-- 属性前加:可将字符串字符串变成变量 -->
                    <img class="img-fluid" :src="user.photo" alt="">
                </div>
                <div class="col-9">
                    <!-- <div class="username">{{ fullName }}</div> -->
                    <div class="username">{{ user.username }}</div>
                    <div class="fans">粉丝: {{ user.followerCount }}</div>
                    <!-- v-on:click的简写是@click -->
                    <button @click="follow" v-if="!user.is_followed" type="button" class="btn btn-secondary btn-sm">+关注</button>
                    <button @click="unfollow" v-if="user.is_followed" type="button" class="btn btn-secondary btn-sm">取消关注</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { computed } from 'vue';
    // import $ from 'jquery';
    import axios from 'axios';
    import qs from 'qs';
    import { useStore } from 'vuex';

    export default {
        name: "UserProfileInfo",
        props: {
            user: {
                type: Object,
                required: true,
            },
        },
        setup(props, context) {
            let fullName = computed(() => props.user.lastName + ' ' + props.user.firstName);
            const store = useStore();

            const follow = () => {
                /*
                $.ajax({
                    url: "https://app165.acapp.acwing.com.cn/myspace/follow/",
                    type: "POST",
                    data: {
                        target_id: props.user.id,
                    },
                    headers: {
                        'Authorization': "Bearer " + store.state.user.access,
                    },
                    success(resp) {
                        if(resp.result === "success") {
                            // 绑定(调用)父组件函数, 参数填写父组件定义子组件中的@后的名字
                            context.emit("follow");
                        }
                    }
                });
                */
                axios
                    .post('https://game.zzqahm.top/myspace/follow/',
                        // qs包装是为了表单提交
                        qs.stringify({ target_id: props.user.id }),
                        {
                            headers: {
                                'Authorization': "Bearer " + store.state.user.access,
                            }
                        })
                    .then(resp => {
                        if(resp.data.result === "success") {
                            // 绑定(调用)父组件函数, 参数填写父组件定义子组件中的@后的名字
                            context.emit("follow");
                        }
                    })
                    .catch(error => {
                        store.commit('judgeError', error.response.status);
                    });
            }
            const unfollow = () => {
                /*
                $.ajax({
                    url: "https://app165.acapp.acwing.com.cn/myspace/follow/",
                    type: "POST",
                    data: {
                        target_id: props.user.id,
                    },
                    headers: {
                        'Authorization': "Bearer " + store.state.user.access,
                    },
                    success(resp) {
                        if(resp.result === "success") {
                            // 绑定父组件函数, 参数填写父组件定义子组件中的@后的名字
                            context.emit("unfollow");
                        }
                    }
                });
                */
                axios
                    .post('https://game.zzqahm.top/myspace/follow/',
                        qs.stringify({ target_id: props.user.id }),
                        {
                            headers: {
                                'Authorization': "Bearer " + store.state.user.access,
                            }
                        })
                    .then(resp => {
                        if(resp.data.result === "success") {
                            // 绑定父组件函数, 参数填写父组件定义子组件中的@后的名字
                            context.emit("unfollow");
                        }
                    })
                    .catch(error => {
                        store.commit('judgeError', error.response.status);
                    });
            }

            return {
                fullName,
                follow,
                unfollow,
            }
        }
    }
</script>

<style scoped>
     img {
        border-radius: 50%;
     }

     .username {
         font-weight: bold;
     }

     .fans {
         font-size: 12px;
         color: gray;
     }

     button {
         padding: 2px 4px;
         font-size: 12px;
     }

     .img-field {
        display: flex;
        flex-direction: column;
        justify-content: center;
     }
</style>