<template>
    <ContentBase>
        <div class="row justify-content-md-center">
            <div class="col-3">
                <!-- 表单绑定提交函数: @submit="函数名"  -->
                <!-- 表单绑定提交函数并且不刷新: @submit.prevent="函数名" -->
                <!-- js写法: 在函数里传参e, e.preventDefault() -->
                <form @submit.prevent="register">
                    <div class="mb-3">
                        <label for="username" class="form-label">用户名</label>
                        <!-- v-model: 将变量username的值与input的值绑定 -->
                        <input v-model="username" type="text" class="form-control" id="username">
                    </div>
                    <div class="mb-3">
                        <label for="password" class="form-label">密码</label>
                        <input v-model="password" type="password" class="form-control" id="password">
                    </div>
                    <div class="mb-3">
                        <label for="password_confirm" class="form-label">确认密码</label>
                        <input v-model="password_confirm" type="password" class="form-control" id="password_confirm">
                    </div>
                    <div class="error-message">
                        {{
                            error_message
                        }}
                    </div>
                    <button type="submit" class="btn btn-primary">注册</button>
                </form>
            </div>
        </div>
    </ContentBase>
</template>

<script>
    import ContentBase from '../components/ContentBase';
    import { ref } from 'vue';
    import { useStore } from 'vuex';
    // @ 指的是src目录
    import router from '@/router/index';
    // import $ from 'jquery';
    import axios from 'axios';

    export default {
        name: 'RegisterView',
        components: {
            ContentBase,
        },
        setup() {
            const store = useStore();
            let username = ref('');
            let password = ref('');
            let password_confirm = ref('');
            let error_message = ref('');

            const register = () => {
                error_message.value = "";
                /*
                $.ajax({
                    url: "https://game.zzqahm.top/myspace/user/",
                    type: "POST",
                    data: {
                        username: username.value,
                        password: password.value,
                        password_confirm: password_confirm.value,
                    },
                    success(resp) {
                        if(resp.result === "success") {         
                            store.dispatch("login", {
                                username: username.value,
                                password: password.value,
                                success() {
                                    // 跳转页面
                                    router.push({name: 'userlist'});
                                },
                                error() {
                                    error_message.value = "系统异常，请稍后重试！";
                                }
                            });
                        } else {
                            error_message.value = resp.result;
                        }
                    }
                });
                */
                const objToParams = obj => {
                    let arr = [];
                    for(let key in obj) {
                        arr.push(`${key}=${obj[key]}`);
                    }
                    return arr.join('&');
                }
                
                let user = {
                    username: username.value,
                    password: password.value,
                    password_confirm: password_confirm.value,
                };

                axios
                    .post('https://game.zzqahm.top/myspace/user/', 
                        objToParams(user)
                    )
                    .then(resp => {
                        if(resp.data.result === "success") {
                            store.dispatch("login", {
                                username: username.value,
                                password: password.value,
                                success() {
                                    // 跳转页面
                                    router.push({name: 'userlist'});
                                },
                                error() {
                                    error_message.value = "系统异常，请稍后重试！";
                                }
                            });
                        } else {
                            error_message.value = resp.data.result;
                        }
                    });
            };

            return {
                username,
                password,
                password_confirm,
                error_message,
                register,
            }
        }
    }
</script>

<style scoped>
    button {
        width: 100%;
    }
    .error-message {
        color: red;
    }
</style>