<template>
    <ContentBase>
        <div class="row">
            <div class="col-3">
                <!-- :user的全称是v-bind:user -->
                <!-- 子组件向父组件传递信息，子组件应该触发父组件的函数，写法是: v-on:click="", 简写为: @自定义名字="父组件函数名" -->
                <UserProfileInfo @follow="follow" @unfollow="unfollow" :user="user" />
                <UserProfileWrite v-if="is_me"  @post_a_post="post_a_post" />
            </div>
            <div class="col-9">
                <UserProfilePosts :user="user" :posts="posts" @delete_a_post="delete_a_post" />
            </div>
        </div>
    </ContentBase>
</template>

<script>
    import ContentBase from '../components/ContentBase';
    import UserProfileInfo from '../components/UserProfileInfo';
    import UserProfilePosts from '../components/UserProfilePosts';
    import UserProfileWrite from '../components/UserProfileWrite';
    import { reactive } from 'vue'; 
    import { useRoute } from 'vue-router';
    // import $ from 'jquery';
    import axios from 'axios';
    import { useStore } from 'vuex';
    import { computed } from 'vue';

    export default {
        name: 'UserProfileView',
        components: {
            ContentBase,
            UserProfileInfo,
            UserProfilePosts,
            UserProfileWrite,
        },
        /**
         * 一个属性是函数时可简写, 完整形式:
         * setup: () => {
         * 
         * }
         */
        setup() {
            const store = useStore();
            const route = useRoute();
            // 获取路径中的参数
            const userId = parseInt(route.params.userId);

            const user = reactive({});
            const posts = reactive({});
            /*
            $.ajax({
                url: "https://app165.acapp.acwing.com.cn/myspace/getinfo/",
                type: "GET",
                data: {
                    user_id: userId,
                },
                headers: {
                    'Authorization': "Bearer " + store.state.user.access,
                },
                success(resp) {
                    user.id = resp.id;
                    user.username = resp.username;
                    user.photo = resp.photo;
                    user.followerCount = resp.followerCount;
                    user.is_followed = resp.is_followed;
                }
            });
            */
            axios
                .get('https://game.zzqahm.top/myspace/getinfo/', {
                    params: {
                        user_id: userId,
                    },
                    headers: {
                        "Authorization": "Bearer " + store.state.user.access,
                    }
                })
                .then(resp => {
                    user.id = resp.data.id;
                    user.username = resp.data.username;
                    user.photo = resp.data.photo;
                    user.followerCount = resp.data.followerCount;
                    user.is_followed = resp.data.is_followed;
                })
                .catch(error => {
                    store.commit('judgeError', error.response.status);
                });
            /*
            $.ajax({
                url: "https://app165.acapp.acwing.com.cn/myspace/post/",
                type: "GET",
                data: {
                    user_id: userId,
                },
                headers: {
                    'Authorization': "Bearer " + store.state.user.access,
                },
                success(resp) {
                    posts.count = resp.length;
                    posts.posts = resp;
                }
            });
            */
            axios
                .get('https://game.zzqahm.top/myspace/post/', {
                    params: {
                        user_id: userId,
                    },
                    headers: {
                        "Authorization": "Bearer " + store.state.user.access,
                    }
                })
                .then(resp => {
                    posts.count = resp.data.length;
                    posts.posts = resp.data;
                })
                .catch(error => {
                    store.commit('judgeError', error.response.status);
                });

            const follow = () => {
                if(user.is_followed) return;
                user.is_followed = true;
                user.followerCount++;
            };

            const unfollow = () => {
                if(!user.is_followed) return;
                user.is_followed = false;
                user.followerCount--;
            };

            const post_a_post = content => {
                posts.count++;
                posts.posts.unshift({
                    id: posts.count,
                    userId: 1,
                    content: content,
                });
            };

            const delete_a_post = post_id => {
                // filter内为true保留，false删除
                posts.posts = posts.posts.filter(post => post.id !== post_id);
                posts.count = posts.posts.length;
            };

            const is_me = computed(() => userId === store.state.user.id);

            return {
                user, // 这是简写, 全称: user: user,
                follow,
                unfollow,
                posts,
                post_a_post,
                delete_a_post,
                is_me,
            }
        }
    }
</script>

