import { createStore } from 'vuex'
import ModelUser from './user';

export default createStore({
    // 存储数据
    // 若想调用store中state中的属性需要使用 $store.state
    state: {
        // user: {
        //     username: "",
        //     id: "",
        //     firstName: "",
        //     lastName: "",
        // },
    },
    // 定义需要获取且需要经过处理的数据(不能修改state数据只能读取state数据)则写到此处
    getters: {
        // fullName(state) {
        //     return state.user.firstName + state.user.lastName;
        // }
    },
    // 定义对state数据直接修改的操作(不能执行异步操作)
    // 使用mutations的函数：store.commit()     // 例子在user.js的actions中 和 NavBar的Script中
    mutations: {
        // updateUser(state, user) {
        //     state.user.username = user.username;
        // }
        judgeError(state, error) {
            if(error === 401) {
                // 清空信息
                localStorage.clear();
                // 清空cookie
                let d = new Date();
                d.setTime(d.getTime() + -1);
                document.cookie = "user=user; expires=" + d.toUTCString() + "; path=/";
                // 强制下线
                this.commit('logout');
            }
        }
    },
    // 定义对state数据的操作(完整的复杂的修改, 支持异步)(注意不能直接修改state，只能通过mutations修改state)
    // 使用action的函数：store.dispatch()   // 例子在LoginView.vue
    actions: {
    },
    // 分割state数据, 使state简洁
    // 访问user: store.state.user.username     // 例子在NavBar.vue 和 UserListView中的script中
    modules: {
        user: ModelUser,

    }
});